import Vue from "vue"

Vue.directive('panel', {
  twoWay: true,
  priority: 1000,

  params: [
    
  ],
    
  bind(obj) {
    

    

  },
  update(obj) {
    var _self = this,
       k=null
    if(obj.isbutton){//

      var pIcon = $(this.el).find("#pIcon");
      var  buttons = $(this.el).find("button");
     
          function fn1  (){
             _self.set(  {key:true,isbutton: true})
                  pIcon.off("click",)
                  buttons.on("click",fn2)
          }
         
         function fn2  (){
             _self.set(  {key:false,isbutton: true})
                  pIcon.on("click",fn1)
                  buttons.off("click")
          }

         pIcon.on("click",fn1)

         


    }else{//
          
          if(obj.key){
             k = 1 
          }else{
              k = 0
          }
          $(this.el).find("#pIcon").on("click",function(){
             k+=1
              if(k%2 === 0){
                  _self.set(  {key:false,isbutton: false})

              }else{
                  _self.set(  {key:true,isbutton: false})
                  console.log(1)
                
              }

      })
  }
  
     
   


  },
  unbind: function () {
   $(this.el).find("#pIcon").off("click")
  
   
  }
})




